

.contact__container {
    width: 80vw;
    height: 50vh;
    background-image: url('../../assets/mailmin.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    border-radius: 2rem;
    margin: auto;
    margin-bottom: 3vh;
}



form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 1rem auto;
    width: 80%;
    gap: 2rem;
}

input, textarea {
    width: 100%;
    margin: auto;
    margin-top: 2rem;

    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary);
    resize: none;
    color: var(--color-light);
}

/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
   .contact__container {
    grid-template-columns: 1fr;
    width: 80vw;
    height: 60vh;
    gap: 0;
    
   }

   #contact {
    position: relative;
    /* bottom: 15vh; */
   }

    form {
        grid-template-columns: 1fr;
        gap: 0;
        padding: 1rem;
    }


}


/* media queries (phones) */ 

@media screen and (max-width: 600px) {

    
    input, textarea {
        padding: 0.6rem;
        margin-top: 3rem;
    }
    

   
}