
  
  
  p {
    text-shadow: 0 0 7px rgba(255,255,255,.3), 0 0 3px rgba(255,255,255,.3);
  }
    



header {
    height: 100vh;
    padding-top: 0;
    margin-bottom: 5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-right: 0;
    margin-left: 0;
    margin: auto;
    width: 100vw;
   
    
    
}

.leftcontainer {
    display: flex;
    flex-direction: column;
    gap: 1vh;
    align-items: center;
    margin-left: 2.5vw;
    margin-top: 30vh;
    
}


.leftcontainer h1{
    text-align: center;
    font-size: 2rem2;
    line-height: 1;
    position: relative;
    
    
    
    text-shadow: 0 0 7px rgba(255,255,255,.3), 0 0 3px rgba(255,255,255,.3);
    
}

.firstanim {
    animation: slideInDown;
    animation-duration: 2s;
    color: var(--color-primary);

    
}

.secondanim {
    animation: slideInLeft;
    animation-duration: 3s;
    color: var(--color-light);
}

.thirdanim {
    animation: slideInUp;
    animation-duration: 4s;
    color: var(--color-primary);
}

@keyframes slideInDown {
    from {
      transform: translate3d(0, -100%, 0);
      visibility: visible;
      opacity: 0;
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }

  @keyframes slideInUp {
    from {
      transform: translate3d(0, 100%, 0);
      visibility: visible;
      opacity: 0;
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }

  @keyframes slideInLeft {
    from {
      transform: translate3d(-100%, 0, 0);
      visibility: visible;
      opacity: 0;
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }



/* CTA */



.cta {
    display: flex;
    gap: 0.4vw;
    justify-content: center;
    flex-direction:row;
    align-items: center;
    font-size: 0.8rem;

    

}

/* SOCIALS */

.header__socials {
    display: flex;
    flex-direction: row;
    gap: 0.8vw;
    justify-content: center;
    font-size: 1.5rem;
    margin-top: 8vh;
}


.header__socials a:hover {
    transform: scale(1.5);
    color: var(--color-primary);
    
}



/* IMAGE */

.me {
    position: relative;
    right: 0;
    top: 0;
    margin: auto;
    overflow: hidden;
    padding: 0;
    width: 100%;
    height: 70vh;
    -webkit-box-shadow: 0px 0px 100px 100px rgba(55, 69, 76, 1);
-moz-box-shadow: 0px 0px 100px 100px rgba(55, 69, 76, 1);
box-shadow: 0px 0px 100px 100px rgba(55, 69, 76, 1);
    border-radius: 10%;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    
    
}

.me > img {
    border: 1px solid transparent;
    opacity: 0.5;
    position: absolute;
}

.logo__container {
    width: 20rem;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 1rem;
    margin-left: 1rem;
    top: 0;
    display: flex;
    
    
}

.logo__container > img {
    border-radius: 12rem;
}

header::after {
    content: "";
    background: url("../../assets/niebomin.png");
    background-size:cover;
    background-repeat: no-repeat;
    opacity: 0.2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;

}




/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .logo__container{
        width: 10rem;
        margin-top: 0.4rem;
        margin-left: 0.4rem;
    }

    header {
        grid-template-columns: 1fr;
        height: 100vh;
        align-self: center;

    }

    .me {
        width: 90vw;
        align-self: center;
        opacity: 0.2;
        transform: translateY(-80%);
       }
       
       .me img {
        width: 100%;
        margin-top: 4vh;

      }

        .cta {
        position: relative;
        top: 10rem;
      }

      .header__socials {
        position: relative;
        top: 10rem;
    }
    .leftcontainer h1{
      position: relative;
      top: 10%;
      
      
  }
}
/* media queries (phones) */ 

@media screen and (max-width: 600px) {
   header {
    grid-template-columns: 1fr;
    height: 70vh;
    width: 80vw;
   }

   .me {
    width: 90%;
    align-self: center;
    margin: auto;
    z-index: -2;
    transform: translateY(-50%);

   }
   
   .me img {
    width: 100%;
    margin-top: 4vh;
    border-radius: 10%;
   }

    .cta {
      top: 18rem;
    }

    .header__socials {
      top: 18rem;
    }
    


    
   

   .leftcontainer {
    display: flex;
    flex-direction: column;
    margin: 0;
    }

  


}