.skills__container {
    display: grid;
    border: 1px solid transparent;
    border-radius: 2rem;
    grid-template-columns: 1fr 1fr;
    gap: 2vw;
    background-image: url('../../assets/techmin.png');
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    margin:1vw;
    height: 60vh;
    width: 80vw;
    overflow: auto;
    position: relative;
    top: 1vh;
    overflow: hidden;
    margin: auto;
    
}
    

.tech__skills, .soft__skills {
    /* background-color: var(--color-primary); */
    padding: 2vh 2vw;
    border-bottom-left-radius: 2rem;
    border-top-left-radius: 2rem;
    margin: 2vh 2vw 2vh 2vw;
    background-image: url('../../assets/techmin.png');
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    overflow-y: scroll;

}






.soft__skills .skills__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
}



.skills__container > div:hover {
    background-color: transparent;
    /* border-color: var(--color-primary); */
    transition: var(--transition);
    
}

.skills__container > div h3 {
    text-align: center;
    margin-bottom: 1vh;
    color: var(--color-light);
    
    
}


.skills__content {
    /* text-align: center; */
    text-shadow: 0 0 7px rgba(47, 62, 70,.3), 0 0 3px rgba(47, 62, 70,.3);
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
       
}


.gap2 {
    gap: 2rem;
}

.gap5 {
    gap: 5rem;
}





.techskills__details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid transparent;
    border-radius: 1rem;
    opacity: 0.7;
    background-color: var(--color-primary);
    width: 100%;
    height: 0;
    padding-top: 100%;
    margin-left: 0.4rem;
    margin-right: 0.7rem;
    

}

.softskills__details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid transparent;
    border-radius: 1rem;
    opacity: 0.7;
    background-color: var(--color-primary);
    width: 100%;
    height: 0;
    padding-top: 100%;
    margin: 0;
    

}





.skills__details:hover {
    border-color: var(--color-primary);
    background-color: var(--color-primary);
    transition: var(--transition);
    transform: scale(1.1);
    opacity: 1;
    box-shadow: 8px 8px 24px 0px rgba(53, 79, 82, 1);    

    
}

.skills__details-icon {
    text-align: center;
    margin-top: 6px;
    color: white;
    font-size: 1.5rem;
    
}


ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;  
    
}

li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}



.top-100 {
    margin-top: -250%;
}



li > h4, .gap2 > h4 {
    text-align: center;
    color: var(--color-light);
}


/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .skills__container {
        grid-template-columns: 1fr;
        width: 100vw;
    }

    .skills__container > div {
        width: 80%;
        margin: 0 auto;
    }
    .top-100 {
        margin-top: 0;
    }

  
}



/* media queries (phones) */ 

@media screen and (max-width: 600px) {
    .skills__container {
        gap: 1rem;
        grid-template-columns: 1fr;
        width: 80vw;
        
        margin: 0 auto;

    }
    
    .skills__container > div{
        width: 100%;
        padding: 2rem 1rem;
        font-size: 0.8rem;
    }

    .skills__details {
        font-size: 0.6rem;
    }

   

    .top-100 {
        margin-top: 0;
    }
    }