footer {
    /* height: 20vh; */
    background-color: var(--color-primary);
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
    position: relative;
    bottom: 0;
    padding: 0;

    
}

/* LEFT SIDE */

footer div {
    /* border: 1px solid red; */
}

.footercontact {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0;
    
}

.footercontact__header {
    color: var(--color-bg);
}

.footercontact__icons {
    display: flex;
    gap: 2rem;
    font-size: 2rem;
    

}

.footercontact__icons a:hover {
    transform: scale(1.1);
    color: var(--color-bg);
    transition: all 1.5s;
}



/* CENTER */

.footerlogo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.footerlogo img {
    border-radius: 100%;
    width: 58%;
}

.copyright {
    font-size: 0.4rem;
}

/* RIGHT SIDE */

.footersocials {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}

.footersocials__header {
    color: var(--color-bg);
}

.footersocials__icons {
    display: flex;
    gap: 2rem;
    font-size: 2rem;
}

.footersocials__icons a:hover {
    transform: scale(1.1);
    color: var(--color-bg);
    transition: all 1.5s;
}


/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .copyright {
        font-size: 0.3rem;
        margin-top: 0;
    }

    footer {
        /* height: 20vh; */
        /* z-index: 2; */
    }
   

  
}

/* media queries (phones) */ 

@media screen and (max-width: 600px) {
    

    footer {
        bottom: 0;
        position: relative;
    }

    .footercontact__icons, .footersocials__icons {
        font-size: 1.5rem;
        gap: 1rem;
    }

    

}



