.projects__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    gap: 1.5rem;
    background-image: url('../../assets/projectsmin.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 2rem;
    height: 60vh;
    width: 80vw;
    overflow-y: auto;
    overflow-x: hidden;
    margin: auto;
    
   
    

    
}

.projects__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    background: var(--color-primary);
    background-image: url('../../assets/blog2.png');
    background-size: cover;
    background-position: center;
    border-radius: 2rem;
    border: 1px solid var(--color-light);
    transition: var(--transition);
    opacity: 0.7;
    text-shadow: 0 0 7px rgba(47, 62, 70,.3), 0 0 3px rgba(47, 62, 70,.3);
    margin-top: 2rem;
    font-size: 0.8rem;
}

.projects__item:hover {
    transform: scale(1.1);
    opacity: 1;
    box-shadow: 8px 8px 24px 0px rgba(53, 79, 82, 1);
    border-color: transparent;
}



.projects__item h3 {
    text-align: center;
    margin-top: -20%;
    /* margin: 1.2rem 0 1rem; */
}

.projects__item h4 {
    text-align: center;
    /* margin: 1.2rem 0 1rem; */
    color: var(--color-bg);
}

.projects__item-buttons {
    display: flex;
    gap: 0;
    margin-bottom: 0rem;
    justify-content: center;
    font-size: 0.6rem;
    padding: 0;
}

.projects__item-buttons a {
    border: 1px solid var(--color-light);
}

/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .projects__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
        /* width: 100vw; */
    }

    .projects__item {
        font-size: 0.6rem;
    }

    .projects__item-buttons {
        margin-bottom: 0;
    }
      
}

/* media queries (phones) */ 

@media screen and (max-width: 600px) {
    .projects__container {
        grid-template-columns: 1fr;
        gap: 1rem;
        
    }

    .projects__item-buttons {
        font-size: 0.7rem;
    }

    .projects__item {
        
        font-size: 0.8rem;
    }
    
}