

nav {
    width: 6vw;
    background: rgba(0, 0, 0, 0.3);
    z-index: 3;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 0;
    backdrop-filter: blur(15px);
    left: 0;
    top: 0;
    text-align: center;
    margin-top: 2vh;
    margin-left: 1vw;
    border-left: 1vw solid rgba(0, 0, 0, 0.3);
    border-radius: 10%;
    

}

nav img {
    width: 140%;
    border-top-right-radius: 10%;
    border-bottom-right-radius: 39%;
    padding-bottom: 2vh;
}

nav a:hover {
    transform: scale(1.1);
}

nav a.active {
    color: var(--color-primary);
    transform: scale(1.1);
}

.navbtn  {
    background: transparent;
    justify-content: center;
    text-align: center;
    display: flex;
    font-size: 1rem;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 1vh 2vw 1vh 2vw;
    padding: 0 0 0.6rem 0
    
}

.navbtn h2 {
    font-size: 0.5rem;
    text-align: center;
    
}








.logo__container > img {
    /* border-radius: 12rem; */
}

@media screen and (max-width: 1024px) {
    nav {
       width: 8vw; 
    }

   

}

