.container__logo {
    top: 0;
    max-width:50%;
    height: auto;
    display: flex;
    align-items: center;
    border: 1px solid red;
}

.logo {
    display: flex;
    align-items: center;
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    background-image: url('../../assets/beachmin.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    border-radius: 2rem;
    text-align: center;
    height: 60vh;
    width: 80vw;
    margin: auto;
    
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;
    
    

}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    margin: 2vw;
}

.about__card {
    background: var(--color-primary);
    opacity: 0.7;
    border-radius: 2vw;
    padding: 1vh;
    text-align: center;
    color: var(--color-light);
    text-shadow: 0 0 7px rgba(47, 62, 70,.3), 0 0 3px rgba(47, 62, 70,.3);

}

.skillscard {
    background-image: url('../../assets/techmin.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.projectscard {
    background-image: url('../../assets/projectsmin.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.expcard {
    background-image: url('../../assets/niebomin.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.about__card:hover {
    border-color: var(--color-primary);
    transition: var(--transition);
    transform: scale(1.1);
    opacity: 1;
    box-shadow: 8px 8px 24px 0px rgba(53, 79, 82, 1);  
}

.about__icon {
    color: var(--color-light);
    font-size: 1.4rem;
    margin-bottom: 0;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card h2 {
    font-size: 0.8rem;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
    text-align: justify;
    margin-right: 2rem;
}

/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
        margin: auto;
        overflow-y: scroll;
        align-items: center;
    
    }

    .about__me {
        width: 50%;
        margin: 3rem auto 6rem;
        
    }


    .about__content p {
        margin: 1rem;
    }

    .about__me-image {
        margin-top:10vh;
        width: 90%;
    }

    .about__cards {
        gap: 2rem;
        margin-right: 5rem;
        margin-left: 5rem;
        grid-template-columns: 1fr 1fr;
    }

    .centeredicon {
        transform: translateX(59%);
    }
    
}

/* media queries (phones) */ 

@media screen and (max-width: 600px) {
.about__container {
    overflow-x: hidden;
}

    .about__me {
        width: 50%;
        /* margin: 0 auto 3rem; */
    }


    .about__cards {
        width: 80%;
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
        margin-right: 2rem;
        margin-left: 2rem;
        /* margin: 8rem; */
    }

    .about_card {
        padding: 0;
    }


    .about__content p {
        margin: 3vh 3vw 3vh 3vw
    }

    .about__me-image {
        margin-top:3vh;
        width: 120%;
    }

    .centeredicon {
        transform: translateX(60%);
    }


}