@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    

    /* background: #000 !important;
  color: #0f0 !important;
  outline: solid #f00 1px !important; */

}

:root {
    --color-bg: #2f3e46;
    --color-bg-variant: #2c2c6c;
    --color-primary: #6B9080;
    --color-primary-variant: #52796F;
    --color-white: #cad2c5;
    --color-light: #CAD2C5;

    --transition: all 400ms ease;
    

    --container-witdth-lg: 80vw;
    --container-width-md: 80vw;
    --container-width-sm: 80vw;

    --border-radius-responsive:  max(0px, min(100%, (100% - 400px + 1px) * 9999)) / 100%;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: var(--color-white);
    opacity: 1; /* Firefox */
  }
  
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: var(--color-white);
  }
  
  ::-ms-input-placeholder { /* Microsoft Edge */
    color: var(--color-white);
  }

html {
    scroll-behavior: smooth;
    font-size: calc(15px + 0.390625vw);
    overflow: hidden;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
    background-color: var(--color-primary);
    border-radius: 10vw;
    border: 1px solid transparent;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: var(--color-light);
    border-radius: 10vw;
    
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: var(--color-primary);
    border-radius: 10vw;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: var(--color-primary-variant);
  }

body {
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(var(--color-bg), #354F52);
    line-height: 1.7;
    overflow: hidden;
    /* display: flex;
    justify-content: center; */
}



/* general styles */




.flex__centered {
    display: flex;
    justify-content: center;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2,5rem;
}

section {
    margin-top: 100vh;
    
}

section > h2, section >h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-light);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-light);
    padding: 0.75vw 1.2vw;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-light);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

.top__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-light);
    font-size: 3vh;
    padding: 1vw;
    
}

.content {
    margin-top: -100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
   
}

.square {
    width: 100%;
    height: 0;
    padding-top: 100%;
}

.square50 {
    width: 50%;
    height: 0;
    padding-top: 50%;
}

.top-100fix {
    margin-top: -100%;
}

.top-50fix {
    margin-top: -60%;
}


